.offer {
    
    display: flex;
    flex-direction: column;
    font-family: "Open Sans";
    .secContainer {
      gap: 1rem;
      margin-top: 4rem;
      
      .mainContent {
        display: contents;
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  
        .singleOffer {
          display: flex;
          flex-direction: column;
          border-radius: 1rem;
          overflow: hidden;
          padding: 0.5rem;
          box-shadow: 0 2px 8px 2px rgba(178, 178, 178, 0.189);
  
          .icon {
            font-size: 20px;
            color: var(--textColor);
            
             &:hover{
              cursor: default;
             }

          }
  
          small {
            font-weight: 500;
            color: var(--textColor);
          
          }
  
          .desImage {
            position: relative;
            height: 200px;
            width: 100%;
            border-radius: 1rem;
            overflow: hidden;
  
            img {
              height: 100%;
              width: 100%;
            }
  
            .discount {
              position: absolute;
              top: 10px;
              right: 10px;
              padding: 5px 10px;
              background: var(--whiteColor);
              color: var(--blackColor);
              border-radius: 3rem;
              font-size: 10px;
              font-weight: 500;
            }
          }
  
          .offerBody {
            padding: 1rem;
            display: flex;
            flex-direction: column;
  
            .price {
              display: flex;
              justify-content: center;
  
              h4 {
                color: var(--blackColor);
                font-weight: 300;
                font-size: 1.1rem;
              }
  
              .status {
                padding: 5px 10px;
                color: var(--HoverColor);
                background: var(--greyBg);
                font-size: 10px;
                font-weight: 600;
                border-radius: 3rem;
              }
            }
  
            .amenities {
              display: flex;
              width: 100%;
              flex-wrap: wrap;
              justify-content: flex-start;
              padding: 1rem;
              gap: 0.5rem;
  
              .singleAmenity {
                padding: 5px 10px;
                border-radius: 5px;
                background: var(--greyBg);
                gap: 0.5rem;
              }
            }
  
            .btndiv {
              display: flex;
              width: 100%;
              padding-left: 1rem;
              gap: 2rem;
              
             .btngg{
              border: none;
              background-color: #deb140;
              border-radius: 5px;
              cursor: pointer;
              padding: 5px 10px;
              display: flex;
              align-items: center;
              
              &:hover{
                border: 0cap;
                background-color: #a98326;
                color: aliceblue;
               }
            }
              
              }
          }
  
          &:hover {
            transform: translateY(-7px);
          }
        }
      }
    }
  
    /* Pagination Styles */
    .pagination {
      display: flex;
      list-style: none;
      justify-content: center;
      padding: 0;
      margin: 20px 0;
    }
  
    .page-item {
      margin: 0 5px;
    }
  
    .page-item a {
      padding: 8px 16px;
      text-decoration: none;
      color: var(--textColor);
      background: var(--greyBg);
      border-radius: 4px;
      cursor: pointer;
    }
  
    .page-item.active a {
      background: var(--whiteColor);
      color: var(--PrimaryColor);
    }
  }
  